import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import {FileUploadModule} from 'primeng/primeng';

import { AppComponent } from './app.component';
import { AdmindashboardComponent } from './admin/admindashboard/admindashboard.component';
import { UserdashboardComponent } from './user/userdashboard/userdashboard.component';
import { UserlayoutComponent } from './user/shared/userlayout.component';
import { AdminlayoutComponent } from './admin/shared/adminlayout.component';
import { LoginComponent } from './public/login/login.component';
import { PubliclayoutComponent } from './public/shared/publiclayout.component';
import { StoreComponent } from './public/store/store.component';
import { AppRoutingModule } from './app.routing';
import { AuthService } from './services/auth.service';
import { GlobalService } from './services/global.service';
import { ProductCreateComponent } from './admin/product/product-create.component';
import { ProductListComponent } from './admin/product/product-list.component';
import { CategoryListComponent } from './admin/category/category-list.component';
import { CategoryCreateComponent } from './admin/category/category-create.component';
import { ProductService } from './services/product.service';
import { CategoryService } from './services/category.service';
import { Cart } from './models/cart';
import { StoreService } from './services/store.service';
import { CartComponent } from './public/cart/cart.component';
import { User } from './models/user';
import { UnauthorizeComponent } from './public/unauthorize/unauthorize.component';
import { UserAuthGuardService } from './services/user-auth-guard.service';
import { AdminAuthGuardService } from './services/admin-auth-guard.service';
import { NotfoundComponent } from './public/notfound/notfound.component';
import { EqualTextValidator } from "angular2-text-equality-validator";
import { SignupComponent } from './public/signup/signup.component';

@NgModule({
  declarations: [
    AppComponent,
    AdmindashboardComponent,
    UserdashboardComponent,
    UserlayoutComponent,
    AdminlayoutComponent,
    LoginComponent,
    PubliclayoutComponent,
    StoreComponent,
    ProductCreateComponent,
    ProductListComponent,
    CategoryListComponent,
    CategoryCreateComponent,
    CartComponent,
    UnauthorizeComponent,
    NotfoundComponent,
    EqualTextValidator,
    SignupComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    ReactiveFormsModule,
    AppRoutingModule,
    FileUploadModule
  ],
  providers: [AuthService, GlobalService, ProductService, CategoryService, Cart, StoreService, UserAuthGuardService, AdminAuthGuardService],
  bootstrap: [AppComponent]
})
export class AppModule { }



